@import "../../../Resources/Variables.scss";

.HeaderImg{
    
    img{
        width: 100%;
        
    }       
    .header_text{
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.3);
        border:10px solid black;
        background-color:black;
        position: absolute;
        opacity: 0.8;
        width:40%;
        height:30%;
        margin-top: -20%;
        right:0;
        display: flex;
        justify-content: center;
        flex-flow:column;
        
        h1{
            text-align: center;
            color:white;
            font-size: 3rem;
        }
        h2{
            text-align: center;
            color:white;
        }
        
       
       
    }
}