@import "../../Resources/Variables.scss";

.header-info{
    background-color: black;
    img{
        width: 100%;
        height: 100%; 
        object-fit: cover;
        background-repeat: no-repeat;
        position: relative;
        opacity: 0.5;
    }   
    .header-text{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color:white;
    }
}
.grid-container{
    display: grid;
    grid-template: 1fr 1fr 1fr;
        grid-gap: 5px;
    color: $color-text;
    background: $color-background;
    margin:5%;
    > div {
        background-color:$color-background;
        text-align: center;
        margin:5%;
        padding: 2% ;
        font-size: 17px;
    }
    .project1{
        grid-column: 1;
        
    }
    .project2{
        grid-column: 3;
        
    }
    .project3{
        grid-column: 3;
    }
} 